export default {"changement":0,"uuid":"8ab75c53-4364-464b-ae75-14709df4b60e","langs":["en"],"doi":"10.3200/CNES/GRACE.10.2","creators":[{"fullName":"Lemoine, Jean-Michel","givenName":null,"familyName":null,"role":"pointOfContact","nameType":"Personal","identifier":{"type":"ORCID","identifier":"0000-0002-2758-1269"},"affiliations":[{"name":"CNES","identifier":{"type":"ROR","identifier":"04h1h0y33"}}],"email":" Jean-Michel.Lemoine@cnes.fr "}],"title":{"fr":"","en":"GRACE Earh Gravity Field Level 2 Collection"},"publisher":{"fullName":"CNES","email":"","nameType":"Organizational","role":"publisher"},"publicationDate":"2020-09-01","publicationYear":"2020","formats":[],"descriptions":{"Abstract":{"fr":"","en":"The GRACE level-2 products are the main outputs from the GRACE and GRACE-FO missions. They consist of the time average (10-day, monthly) solutions of the Earth gravity field in spherical harmonics.\n\nGRACE is a joint project between the National Aeronautics and Space Administration ( NASA ) and the Deutsches Zentrum f√ºr Luft- und Raumfahrt ( DLR ). The mission has been proposed in 1996 jointly by the University of Texas at Austin, Center for Space Research ( UTCSR ), the German Research Centre for Geosciences ( GFZ ) and the Jet Propulsion Laboratories (JPL) in Pasadena. GRACE was selected in 1997 as second mission in NASA's Earth System Science Pathfinder ( ESSP ) program. As an innovation, the Principal Investigator Prof. Byron Tapley (UTCSR) and his team are ultimately responsible for developing the flight mission hardware from selection to a launch-ready condition, for accomplishing the scientific objectives and delivering the proposed measurements to the broader Earth science community and general public as expediently as possible. Co-Principal Investigator of the mission is Dr. Frank Flechtner (GFZ). Project management and systems engineering activities are carried out by JPL.\n\nGRACE-FO is a joint project between the National Aeronautics and Space Administration ( NASA ) and the GeoForschungsZentrum ( GFZ ) Potsdam. GRACE-FO, launched May 22, 2018, is a successor to the original GRACE mission. The Principal Investigator at NASA JPL is Felix landerer. Co-Principal Investigator of the mission is Dr. Frank Flechtner (GFZ). Project management and systems engineering activities are carried out by JPL."},"Methods":{"fr":"","en":"Based on the time-variable (monthly or 10-day) solutions CNES/GRGS RL04, a mean Earth gravity field model complete to degree and order 300 has been computed: EIGEN-GRGS.RL04.MEAN-FIELD.linear_mean_pole.zero_slope_extrapolation. It contains time-variable coefficients up to degree and order 90. For the higher degree part, it is based on a combined GRACE+GOCE static model: EIGEN-6C. This model has been used for Precise Orbit Determination of the altimetry satellites following the GDR-E standards"}},"inspire":null,"methodScope":"series","mainLang":"en","language":"en","charset":"utf8","resourceType":"Collection of GRACE Level-2 products","identifiers":[{"type":"local identifier","identifier":"CNES:GRACE:RL05 "}],"links":[{"url":"https://grace.obs-mip.fr/variable-models-grace-lageos/grace-solutions-release-05/rl05-download-products/","type":"URL","funct":"download","protocole":"WWW:LINK-1.0-http--link","title":{"fr":"","en":"Product download"},"description":{"fr":null,"en":null}},{"url":"http://thegraceplotter.com/","type":"URL","funct":"information","protocole":"WWW:LINK-1.0-http--link","title":{"fr":"","en":"The Grace Plotter"},"description":{"fr":"","en":"Visualization service"}}],"related":[],"services":[{"url":"https://thegraceplotter.com/data/maps/CNES.RL05.monthly.NONE/semiannual/{z}/{x}/{y}.png","protocole":"XYZ-Tile-Service","title":{"fr":"","en":"Average semiannual of the gravity signal"},"description":{"fr":"","en":"Gravity signal from GRACE and GRACE-FO (in Equivalent Water Heights), average semiannual signal "}}],"subjects":{"discipline":[{"title":{"fr":"","en":"Geodetics"},"type":"discipline","thesaurus":null,"thesaurusId":-1,"code":null},{"title":{"fr":"","en":"Geodynamic"},"type":"discipline","thesaurus":null,"thesaurusId":-1,"code":null}],"variable":[{"title":{"fr":"","en":"Gravitational field"},"type":"theme","thesaurus":null,"thesaurusId":-1,"code":null}],"platform":[],"productType":[],"featureOfInterest":[],"other":[]},"categories":["geoscientificInformation"],"representations":["grid"],"metaContact":{"fullName":"ForM@Ter","email":"contact@poleterresolide.fr","role":"pointOfContact","type":"DataCurator","affiliations":[],"nameType":"Organizational"},"contributors":[],"dates":[],"status":"onGoing","geoLocation":[{"name":null,"west":"-180","east":"180","north":"90","south":"-90"}],"temporalExtent":{"start":"2008-06-01","end":null},"maintenance":"asNeeded","referentiels":[{"name":"WGS 84","link":"http://www.opengis.net/def/crs/EPSG/0/4326"}],"images":[{"url":"https://service.poleterresolide.fr/geonetwork/srv/api/records/e747d804-a5d1-4fd1-bd9b-306a8ebd4904/attachments/grace2.png","title":{"fr":"","en":"Gravity field screenshot"}}],"condition":{"access":"no","use":"unknown"},"rights":{"license":null,"howToCite":null,"inspire":null,"others":[]},"resolutions":[{"value":"100","unit":"km"}]}
